
import { useEffect, useState } from "react";
import { isSameDay } from "date-fns";

import { Shortcut } from "../DatePicker.types";

/**
* @description manages the selectedDates range property for the calendar and the shortcuts array property for the list of shortcut names
* @param selectedDates
* @param shortcuts
*/
const useShortcutName = (selectedDates: Date[], shortcuts: Shortcut[]) => {
	const [selectedShortcutLabel, setSelectedShortcutLabel] = useState<string>();
	const [selectedShortcutId, setSelectedShortcutId] = useState<string>();

	useEffect(() => {
		// add logic to only run when they are cleared
		if (selectedDates.length < 2) {
			setSelectedShortcutLabel(undefined);
			setSelectedShortcutId(undefined);
			return;
		}

		const isShortcut = shortcuts.some(({
			getDates,
			label,
			id,
		}) => {
			const shortcutStartDate = getDates()[0].getTime();
			const selectedDatesStartDate = selectedDates[0].getTime();
			const shortcutEndDate = getDates()[1].getTime();
			const selectedDatesEndDate = selectedDates[1].getTime();

			if (shortcutStartDate === selectedDatesStartDate
				&& shortcutEndDate === selectedDatesEndDate
			) {
				setSelectedShortcutLabel(label);
				setSelectedShortcutId(id);
				return true;
			}

			if (isSameDay(shortcutStartDate, selectedDatesStartDate)
				&& isSameDay(shortcutEndDate, selectedDatesEndDate)
			) {
				setSelectedShortcutLabel(label);
				setSelectedShortcutId(id);
				return true;
			}
			return false;
		});

		if (!isShortcut) {
			setSelectedShortcutLabel(undefined);
			setSelectedShortcutId(undefined);
		}

	}, [selectedDates, shortcuts]);

	return {
		selectedShortcutLabel,
		setSelectedShortcutLabel,
		selectedShortcutId,
		setSelectedShortcutId,
	};
};

export default useShortcutName;
